<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="name">缴款</span>
				</div>
	
			</div>
			<div class="cash_flow_table">
				<div class="under_fund_main table">
					<el-table ref="multipleTable" :data="tableData" border style="width: 100%" max-height='350'>
						<el-table-column v-for="(tableHeaderitem,index) of tableHeader "   :key="index" :prop="tableHeaderitem.param" :label="tableHeaderitem.text" :width='tableHeaderitem.width'></el-table-column>
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {getDistributeDetail} from '../../config/apiSj'
	export default {
		data() {
			return {
				total: 3,
				currentPage: 1,
				pageSize: 10,

				tableHeader:[
					{
						param:'unit',
						text:'币种',
						width:'120',
						type:'text'
					},{
						param:'event_name',
						text:'事件',
						width:'150',
					},{
						param:'type',
						text:'缴款基金类别',
						width:'180',
					},{
						param:'name',
						text:'基金名称',
						width:'250',
					},{
						param:'invest_date',
						text:'出资日期',
						width:'200',
					},{
						param:'subscribe',
						text:'当期应缴金额(万元)',
						width:'200',
					},{
						param:'fee',
						text:'当期实缴金额(万元)',
						width:'200',
					},
				],
				tableData: [],


			}
		},
		created() {
			this.getListCashFlowData();
		},
		computed: {
			
		},
		mounted() {},
		methods: {
			//获取缴款现金流数据
			getListCashFlowData(){
				getDistributeDetail({
					// distribute_id:this.$route.query.id,
					id:this.$route.query.jkId,
					c_type:this.$route.query.c_type,
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data = res.data.payload;
						let newData = [];
						newData.push(data);
						this.tableData = newData;
					}
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
	.body {
		padding: 0 0px 30px 0px;
	}
	
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	
	.company_name_right .delete {
		margin-right: 60px;
		color: #6292F4;
		font-size: 14px;
	}
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}
</style>


